import React, {useState } from "react";
import ReactDOM from "react-dom";
import Tittle from './Tittle';
//class Calculato extends React.Component{
    function Calculator() {
        //creating number
      const[data, setData] = useState("");
      const calcBtns = [];
      [7,8,9,4,5,6,1,2,3,".",0,"%"].forEach(item => {
        calcBtns.push(
          <button onClick={e => {setData(data + e.target.value);
            }}
            value={item}
            key={item}
          >
            {" "}
            {item}
          </button>
        );
      });
        return (
          //operations buttons

          <div className="wrapper">
            {" "}
      
          <div className="show-input">{data}</div>
          <div className="digits flex">{calcBtns}</div>
          <div className="modifiers subgrid">
          
          <button onClick={() => setData(data.substr(0, data.length - 1))}>Clear</button>
          <button onClick={() => setData("")} value="">AC</button>
          </div>
      
           <div className="operations subgrid">
           <button onClick={e => setData(data + e.target.value)} value="+">+</button>
           <button onClick={e => setData(data + e.target.value)} value="-">{" "}-{" "}</button>
           <button onClick={e => setData(data + e.target.value)} value="*">{" "}*</button>
           <button onClick={e => setData(data + e.target.value)} value="/">{" "}/</button>
          
          <button onClick={e => {
                  try {
                    setData(
                      String(eval(data)).length > 3 && String(eval(data)).includes(".")
                        ? String(eval(data).toFixed(4))
                        : String(eval(data))
                    );
                  } catch (err) {
                    console.log(err);
                  }
                }}
                value="="
              >
                 =
              </button>
             </div>
             <div className="inicio">
    <Tittle />
  </div>
          </div>
          
        );
      }
//}
export default Calculator;